body {
  background-color: #9266ff;
}

.App {
  text-align: center;
  background-color: #3f3160;
  min-height: 600px;
  width: 400px;
  margin: 50px auto;
  border-radius: 10px;
}
#to-do-form input{
  background-color: rgb(27, 112, 137);
  width: 200px;
  height: 50px;
  border: none;
  border-radius: 10px;
  outline: none;
  padding: 0 15px;
  margin: 20px;

}
#to-do-form input::placeholder {
  color: rgba(255, 255,255,.5);
  font-size: 18px;
}
#to-do-form input:focus {
  color: rgb(255, 255,255);
  font-size: 18px;
}
#to-do-form button {
  color: black;
  background-color: gold;
  font-weight: 400;
  font-size: 18px;
  border-radius: 10px;
  padding: 10px 20px;
  border: none;
  outline: none;
  cursor: pointer;
}
.h1 {
  color: #3f3160;
  background-color: #9266ff;
  margin: 15px auto;
  width: 200px;
}
.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

