.list {
    color: white;
    width: 300px;
    background-color: #bf07ac;
    height: 45px;
    margin: 20px auto;
    border-radius: 5px;
}
.list p {
    position: relative;
    font-weight: normal;
    text-align: left;
    font-size: 22px;
    padding: 0 15px;
}
.list p span {
    position: absolute;
    right: 10px;
    cursor: pointer;
    margin: 5px 5px;
    font-size: 20px;
}
.faicons {
    margin: 0 5px;
    font-size: 18px;
}